import axios from 'axios'
// axios 
// axios.defaults.timeout = config.TIMEOUT;
// axios.defaults.timeout = 3000;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
axios.defaults.baseURL = '';

const baseurl = 'http://result.eolinker.com/h22MhKy12ed20794d2382464271cb612b61ed1d26903a02?uri=';
// const baseurl = '';

const baseurl2 = 'http://result.eolinker.com/h22MhKy12ed20794d2382464271cb612b61ed1d26903a02?uri=';
// const baseurlMock = 'http://mock.eolinker.com/h22MhKy12ed20794d2382464271cb612b61ed1d26903a02?uri=';

// axios.defaults.withCredentials = true;

export function Get(url, params) {
    return new Promise((resolve, reject) => {
        axios.get(baseurl + url, params)
            .then(response => {
                if (response.data.code!="501") {
                    resolve(response.data);
                } else if(response.data.code=='501') {
                    axios.get(baseurl2 + url, params)
                        .then(response => {
                            resolve(response.data);
                        })
                        .catch((error) => {
                            reject(error)
                        })
                }

            })
            .catch((error) => {
                axios.get(baseurl2 + url, params)
                    .then(response => {
                        resolve(response.data);
                    })
                    .catch((error) => {
                        reject(error)
                    })
            })
    })
}
